.faq .container {
  max-width: 70%;
  border-radius: 5px;
}

.faq .box {
  background-color: white;
  margin-bottom: 20px;
}

.faq button {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 15px 20px;
}

.faq h2 {
  font-weight: 500;
}

.faq p {
  padding: 20px;
}

.faq .accordion:hover {
  background-color: #3A9943;
  color: white;
  cursor: pointer;
  .mark{
    color: white;
  }
}

.mark{
  color: #3A9943;
}